  color: @purpleColor;\n      }\n      .card-finger{\n        position: absolute;\n        top: 29%;\n        left: 35.3%;\n        font-size: 25px;\n        animation: finger 1s ease infinite both;\n        color: @purpleColor;\n      }\n     .icon-right{\n       color: @lightOrangeColor;\n     }\n     .middle-text{\n       width:75%;\n       margin-left: 15px;\n       font-size: 15px;\n       display: flex;\n       flex-wrap: nowrap;\n       align-items: center;\n       overflow: hidden;\n       white-space: nowrap;\n       .card{\n        color: @purpleColor;\n        font-size: 18px;\n         animation: finger 1s ease infinite both;\n       }\n       .remaining-time{\n          color: @orangeColor;\n          display: flex;\n          flex-wrap: nowrap;\n          font-size: 12px;\n          margin-left: 35px;\n          .remaining-icon{\n            display: block;\n            margin-top: -5px;\n          }\n       }\n     }\n     .icon-more{\n       color: @greyColor;\n     }\n     .none-border{\n       border: none;\n     }\n   }\n }\n\n .reminder{\n   width: 100%;\n   margin: 10px auto auto auto;\n   padding: 10px;\n   box-sizing: border-box;\n   display: flex;\n   flex-wrap: nowrap;\n   align-items: center;\n   background-color: @backgroundWhite;\n   .reminder-icon{\n     width: 12%;\n     text-align: center;\n     font-size: 20px;\n     color: @orangeColor;\n     margin-right: 5px;\n   }\n }\n\n .activity-rules-desc{\n   width: 100%;\n   margin: auto;\n   overflow: hidden;\n   margin-top: 10px;\n   box-sizing: border-box;\n   padding: 10px 15px;\n   background-color: @backgroundWhite;\n  h3{\n    margin: 10px 0px;\n  }\n  p{\n    text-align:left;\n    text-align: justify;\n    text-indent:2em;\n    box-sizing: border-box;\n    padding: 5px 0px;\n    line-height: 25px;\n  }\n }\n .van-dialog__header{\n   font-size: 20px !important;\n   color: @redColor;\n }\n .had{\n    padding: 10px 15px;\n    box-sizing: border-box;\n    text-align: center;\n    color: @greyColor;\n }\n .had-text{\n  padding: 0px 10px 15px;\n  text-align: center;\n }\n .show{\n   width: 80%;\n   margin: 50% auto auto auto;\n   background-color: @whiteColor;\n   box-sizing: border-box;\n   border-radius: 10px;\n   h3{\n     height: 40px;\n     line-height: 40px;\n     text-align: center;\n     border-radius: 10px 10px 0px 0px;\n     background-image: linear-gradient(to left, #19D5FD, #1D62F0);\n   }\n   .field{\n     padding: 25px 0px;\n   }\n   .show-but{\n     width: 100%;\n     padding-bottom: 25px;\n     display: flex;\n     flex-wrap: nowrap;\n     justify-content: space-around;\n     .but{\n       width: 38%;\n       height: 35px;\n       border: none;\n       border-radius: 50px;\n       color: @whiteColor;\n       font-size: 16px;\n       background-image: linear-gradient(to left, #19D5FD, #1D62F0);\n     }\n   }\n }\n\n .overlay{\n   .first{\n    padding-bottom: 0px !important;\n    .first-but{\n      width: 100%;\n      height: 40px;\n      border: none;\n      border-radius: 0px 0px 10px 10px;\n      background-color: @blueColor;\n      color: @whiteColor;\n      font-size: 18px;\n    }\n   }\n  .wrapper{\n    width: 80%;\n    margin: 50% auto auto auto;\n    background-color: @whiteColor;\n    border-radius: 10px;\n    padding-bottom: 15px;\n    h3{\n      text-align: center;\n      color: @redColor;\n      padding: 15px 0px 0px 0px;\n    }\n    p{\n      text-align: center;\n      font-size: 16px;\n      margin: 15px 0px;\n    }\n    .p1{\n      color: @greyColor;\n      padding: 0px 12px;\n    }\n    .p-font{\n      text-align:left;\n      text-align: justify;\n    }\n    .p2{\n      color: @blueColor;\n      padding: 0px 10px;\n    }\n    .p3{\n      color: @orangeColor;\n      padding: 0px 10px;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      margin: 25px 0px;\n      .jinbi{\n        font-size: 18px;\n        margin-right: 8px;\n      }\n      .size{\n        font-size: 20px !important;\n        font-weight: 600 !important;\n      }\n      span{\n        font-size: 18px;\n        font-weight: 600;\n      }\n    }\n  }\n  .clear{\n    margin: 50px 0px 0px 45%;\n    color: @whiteColor;\n    font-size: 40px;\n  }\n}\n.dialog{\n  .van-dialog__header{\n    color: @blackColor !important;\n  }\n  .con{\n    padding: 15px;\n    color: @greyColor;\n    line-height: 25px;\n    font-size: 16px;\n    text-align:left;\n    text-align: justify;\n  }\n}\n}\n// IndexEr\n.index-er{\n  .active-area{\n    .area{\n      width: 76% !important;\n      justify-content: left !important;\n      .right-text{\n        max-width: 99px !important;\n      }\n      .right-text{\n       font-size: 20px;\n       margin-left: 8px;\n      }\n    }\n    .duihuan{\n      width: 75px;\n      background-color: @orangeColor;\n      color: @whiteColor;\n      box-sizing: border-box;\n      padding: 5px;\n      border-radius: 5px;\n      text-align: center;\n    }\n  }\n // 进入活动领取奖品提示框\n .prize-guidance{\n   width: 80%;\n   height: 150px;\n   margin: 50% auto auto auto;\n   border-radius: 10px;\n   background-color: @backgroundWhite;\n   box-sizing: border-box;\n   overflow: hidden;\n   padding: 10px;\n   h3{\n     color: @redColor;\n     text-align: center;\n     box-sizing: border-box;\n     margin:12px 0px;\n     font-size: 20px;\n   }\n   .guidance-text{\n     text-align: center;\n     color: @greyColor;\n     margin:12px 0px;\n     font-size: 16px;\n   }\n   .jinruhuodong{\n     text-align: center;\n     color: @blueColor;\n     margin:12px 0px;\n     font-size: 16px;\n   }\n }\n // 关闭按钮\n .close-but{\n  width: 80%;\n  margin: 20px auto auto auto;\n  text-align: center;\n  color: @whiteColor;\n  span{\n    font-size: 40px;\n  }\n }\n}\n\n// 不符合页\n.non-conformity{\n  width: 100%;\n  height: 100%;\n  background-color: @backgroundBlue;\n  .nc-tips{\n    width: 100px;\n    height: 100px;\n    border-radius:50px;\n    background-color: @backgroundWhite;\n    color: @orangeColor;\n    text-align: center;\n    line-height: 100px;\n    font-size: 40px;\n    margin: 150px auto auto auto;\n  }\n  .nc-text{\n    color: @whiteColor;\n    padding: 10px;\n    text-align: center;\n  }\n}\n\n// 订单管理\n.order-admin{\n  width: 100%;\n  overflow: hidden;\n  .order-list{\n    width: 100%;\n    li{\n      width: 100%;\n      box-sizing: border-box;\n      background-color: @backgroundLightGrey;\n      display: flex;\n      flex-wrap: nowrap;\n      justify-content: space-between;\n      align-items: center;\n      padding: 10px;\n      margin-top: 15px;\n      .left{\n        width: 70%;\n        display: flex;\n        flex-direction: column;\n        line-height: 25px;\n        white-space: nowrap;\n        overflow: hidden;\n        .left-top{\n          font-size: 15px;\n          font-weight: 600;\n        }\n        .left-bottom{\n          color: @greyColor;\n        }\n      }\n      .right{\n        width: 20%;\n        height: 32px;\n        border: none;\n        background-color: @lightOrangeColor;\n        color: @whiteColor;\n        border-radius: 5px;\n      }\n    }\n  }\n  .van-overlay{\n  .wrapper{\n    width: 80%;\n    margin: 50% auto auto auto;\n    background-color: @whiteColor;\n    border-radius: 10px;\n    padding-bottom: 15px;\n    h3{\n      text-align: center;\n      color: @redColor;\n      padding: 15px 0px 0px 0px;\n    }\n    p{\n      text-align: center;\n      font-size: 16px;\n      margin: 15px 0px;\n    }\n    .p1{\n      color: @greyColor;\n    }\n    .p2{\n      color: @blueColor;\n      padding: 0px 10px;\n    }\n  }\n  .clear{\n    margin: 50px 0px 0px 45%;\n    color: @whiteColor;\n    font-size: 40px;\n  }\n}\n}\n\n// 我的团队\n.my-team{\n  background-color: @whiteColor;\n  height: 100%;\n  overflow: hidden;\n  .head-img{\n    // height: 100px;\n    img{\n      width: 100%;\n      height: 150px;\n    }\n  }\n\n  .title{\n    text-align: center;\n    font-size: 14px;\n    font-weight: 500;\n    padding: 10px 5px 0px 5px;\n    box-sizing: border-box;\n    color: @greyColor;\n  }\n  .list{\n    width: 100%;\n    box-sizing: border-box;\n    li{\n      width: 90%;\n      // margin:25px auto auto auto;\n      margin:auto;\n      display: flex;\n      flex-wrap: nowrap;\n      align-items: center;\n      border-bottom: 1px solid @backgroundLightGrey;\n      padding: 18px 0px;\n      .list-img{\n        width: 13.335%;\n        height: 45px;\n        border-radius: 50px;\n        margin-right: 15px;\n      }\n      .name-time{\n        display: flex;\n        flex-direction: column;\n        line-height: 21px;\n        overflow: hidden;\n        .name{\n          font-size: 15px;\n          font-weight: 600;\n          white-space: nowrap;\n        }\n        .time{\n          color: @greyColor;\n          white-space: nowrap;\n        }\n      }\n   }\n  }\n}\n\n//我的推荐二维码\n.my-qr-code{\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n  position: relative;\n  .codeImg{\n    width: 100%;\n    height: 100%;\n  }\n  \n}\n\n@keyframes finger{\n  0% {\n    transform: none;\n}\n70% {\n    transform: scale3d(.8, .8, .8);\n}\n100% {\n    transform: none;\n}\n}\n\n@keyframes circle {\n  0% {\n    opacity: 0;\n    transform: scale3d(0, 0, 0);\n}\n70% {\n    opacity: 1;\n    transform: scale3d(1.2, 1.2, 1.2);\n}\n100% {\n    opacity: 0;\n    transform: scale3d(0, 0, 0);\n}\n}\n // 兑换礼品成功页\n .exchange-success{\n   width: 100%;\n   overflow: hidden;\n   .success{\n     width: 100%;\n     margin-top: 45px;\n    .icon-success{\n      display: block;\n      width: 100%;\n      text-align: center;\n      font-size: 100px;\n      color: @greenColor;\n    }\n    p{\n      margin-top: 10px;\n      padding: 0px 10px;\n      text-align: center;\n      font-size: 20px;\n      color: @greenColor;\n    }\n   }\n   .chance-to-draw{\n     width: 100%;\n     margin-top: 30px;\n     p{\n       color: @redColor;\n       text-align: center;\n       font-size: 16px;\n     }\n     .scraping-card{\n      width: 100%;\n      margin-top: 10px;\n      padding: 20px 0;\n      background-color: red;\n      .canvas-box{\n        position: relative;\n        overflow: hidden;\n        width: 85%;\n        height: 120px;\n        border-radius: 10px;\n        background-color: #FFF;\n        margin: auto;\n        display: flex;\n        align-items: center;\n        box-sizing: border-box;\n        .winning-results{\n          width: 100%;\n          height:85%;\n          display: flex;\n          flex-wrap: nowrap;\n          align-items: center;\n          justify-content: center;\n          .winres-text{\n            width: 76%;\n            text-align: center;\n          }\n          .winres-but{\n            width: 20%;\n            height: 105px;\n            font-size: 20px;\n            text-align: center;\n            font-weight: 600;\n            border: none;\n            color: @whiteColor;\n            background-color: @greenColor;\n          }\n        }\n      }\n      #canvas{\n        width: 96%;\n        height:90%;\n        position: absolute;\n        left: 2%;\n        top: 5%;\n        background-color: transparent;\n      }\n     }\n   }\n   .exchange-tips{\n     width: 95%;\n     margin: auto;\n     border: 1px dashed @greyColor;\n     border-radius: 5px;\n     box-sizing: border-box;\n     margin-top: 20px;\n     padding: 10px;\n     display: flex;\n     flex-wrap: nowrap;\n     .tips-text{\n       width: 25%;\n       color: @redColor;\n     }\n     .describe-text{\n      width: 88%;\n      color: @greyColor;\n      line-height: 22px;\n      text-align:left;\n      text-align: justify;\n     }\n   }\n }",".pushmore-index {\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n  overflow-x: hidden;\n  overflow-y: scroll;\n  background-color: #f3f3f3;\n}\n.pushmore-index .head {\n  width: 100%;\n  height: 100px;\n  background-color: #0066cb;\n  color: #fff;\n  display: flex;\n  flex-wrap: nowrap;\n  align-items: center;\n  background-image: linear-gradient(to left, #19